<template>
  <transition name="operation">
    <div v-show="settingStatus" id="setting" class="func-operation">
    </div>
  </transition>
</template>

<script>
export default {
  name: 'setting',
  data () {
    return {
      settingStatus: false
    }
  },
  mounted () {
    this.settingStatus = true
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -o-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation {
      width: calc(100% - 76px);
      margin: 0 38px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation {
      width: calc(100% - 36px);
      margin: 0 18px;
    }
  }
</style>



